.weui-btn_default{
    background-color:#ededed ;
    color: #5b6a91;
    font-weight: normal;
    font-size: 15px;
  }
  .page__bd_spacing {
    display: grid;
    grid-template-columns: auto auto  auto;
    grid-template-rows: auto auto  auto;
    grid-row-gap: 30px;
    border: none!important;
    margin: 20px 60px 0 50px;

    .weui-btn {
      width: 90px;
      margin-right: 6px;
    }
  }
  .weui-cell__bd{
    margin: 10px -11px;
    padding:13px 10px;
    border-radius: 4px;
    //background-color: white;
  }
  .weui-btn:not(.weui-btn_mini)+.weui-btn:not(.weui-btn_mini){
    margin-top: 0!important;
  }
